import { EntityPageConfig } from './EntityPageConfig';
import { CollectionPageConfig } from './CollectionPageConfig';
import { ComputedQuery } from '@wix/patterns';

interface BaseCollectionConfigCommon {
  collectionId: string;
}

interface CMSCollectionConfig extends BaseCollectionConfigCommon {
  entityTypeSource: 'cms';
  custom?: never;
}

interface WixCollectionConfig extends BaseCollectionConfigCommon {
  entityTypeSource: 'wix';
  custom?: never;
}

interface CustomCollectionConfig extends BaseCollectionConfigCommon {
  entityTypeSource: 'custom';
  custom: {
    id: string;
  };
}

export type BaseCollectionConfig =
  | CMSCollectionConfig
  | WixCollectionConfig
  | CustomCollectionConfig;

export type PatternsFieldType =
  | 'SHORT_TEXT'
  | 'LONG_TEXT'
  | 'NUMBER'
  | 'BOOLEAN'
  | 'DATE'
  | 'DATETIME'
  | 'URL'
  | 'ARRAY'
  | 'REFERENCE'
  | 'IMAGE';

export interface AppConfig {
  pages: PageConfig[];
}

export interface BasePageConfig {
  id: string;
  appMainPage?: boolean;
}

export interface CollectionPageConfigType extends BasePageConfig {
  type: 'collectionPage';
  collectionPage: CollectionPageConfig;
  entityPage?: never;
}

export interface EntityPageConfigType extends BasePageConfig {
  type: 'entityPage';
  entityPage: EntityPageConfig;
  collectionPage?: never;
}

export type PageConfig = CollectionPageConfigType | EntityPageConfigType;

export type EntityProps = { [key: string]: any };

export interface IMenuItem<T extends EntityProps = any> {
  item: T;
}
export interface IColumnValue<T> {
  value: T;
  row: Record<string, any>;
}

export type QueryOperator =
  | 'CONTAINS'
  | 'ENDS_WITH'
  | 'EQ'
  | 'EXISTS'
  | 'GT'
  | 'GTE'
  | 'HAS_ALL'
  | 'HAS_SOME'
  | 'LT'
  | 'LTE'
  | 'NE'
  | 'STARTS_WITH'
  | 'URLIZED';

interface BaseField {
  id: string;
  displayName: string;
  validation?: {
    numberRange?: NumberRange;
    stringLengthRange?: StringLengthRange;
    required: boolean;
  };
  capabilities: {
    supportedQueryOperators: QueryOperator[];
    sortable: boolean;
  };
}

export interface ReferenceField extends BaseField {
  type: 'REFERENCE';
  referenceMetadata: {
    referencedCollectionId: string;
  };
}

export interface NonReferenceField extends BaseField {
  type: Exclude<PatternsFieldType, 'REFERENCE'>;
}

export type Field = ReferenceField | NonReferenceField;

export interface NumberRange {
  min?: number;
  max?: number;
}
export interface StringLengthRange {
  minLength?: number;
  maxLength?: number;
}

export interface SchemaConfig {
  id: string;
  fields: Record<string, Field | undefined>;
  displayField: string;
  idField: string;
  imageField?: string;
  actions: {
    get: (entityId: string) => Promise<any>;
    create: (newEntity: Partial<any>) => Promise<any>;
    update: (updatedEntity: any) => Promise<any>;
    delete: (entityId: string) => Promise<any>;
    bulkDelete: (entityIds: string[]) => Promise<any>;
    find: (
      query: ComputedQuery<any>,
      options?: {
        searchableFieldIds?: string[];
        filterFieldMapping?: Record<string, { fieldId: string }>;
      },
    ) => Promise<{ items: any[]; total: number }>;
  };
}
